<template lang="pug">
  .pageGame
    .StatusButton
      button(@click='statusButtonClick' @keypress.enter="preventEnterButton")
        | {{ buttonString }}
      button(@click='changeSpeed' @keypress.enter="preventEnterButton")
        | {{speeds[currentSpeedIndex].name}}
    div#game
      component(v-if="gameStatus" :is='classes[currentClass]' :Message='Message' :Time='time' @timeout='timeoutEvent' :key="_key")
    div
      b-card
        b-card-text {{ summary }}
    div(v-if="error" style="text-align:center;backgroung-color=red;color:red")
      b-card
        span(v-html="error")
      b-table(striped='' hover='' :items='scores' :fields="fields")
      

</template>

<script>
import _ from "lodash";
import TextFade from "./TextFade.vue";
import TextGuess from "./TextGuess.vue";
import words_json from "../assets/words.json";
const words = Object.values(words_json);
// const words_Length = words.length;
const words_WordMaxLength = _.maxBy(words, (word) => word.length).length;
const words_WordMinLength = _.minBy(words, (word) => word.length).length;
// console.log("words", words);
// console.log("words_Length", words_Length);
// console.log("words_WordMaxLength", words_WordMaxLength);
// console.log("words_WordMinLength", words_WordMinLength);

export default {
  name: "PageGame",
  components: {
    TextFade,
    TextGuess
  },
  data() {
    return {
      // not inital values - initial values at initGame()
      Message: "",
      time: 0,
      classes: { show: "TextFade", ask: "TextGuess" },
      currentClass: "",
      gameStatus: false,
      buttonString: "",
      level: 1,
      score: 0,
      show: true,
      error: null,
      scores: [],
      fields: [
        { key: "id", sortable: true },
        { key: "time", sortable: true },
        { key: "level", sortable: true },
        { key: "score", sortable: true }
      ],
      speeds: [
        { speed: 3, name: "slow" },
        { speed: 2, name: "normal" },
        { speed: 1, name: "fast" }
      ],
      currentSpeedIndex: 0
    };
  },
  created() {
    this.initGame();
    this.time = 4;
    this.currentSpeedIndex = 0;
  },
  computed: {
    summary() {
      return `level: ${this.level} , score: ${this.score}`;
    },
    _key() {
      return this.currentClass + this.Message;
    },
    href() {
      return `https://www.dictionary.com/browse/${this.Message}`;
    }
  },
  methods: {
    preventEnterButton(event) {
      event.preventDefault();
    },
    statusButtonClick() {
      if (this.gameStatus) this.error = "game stopped";
      this.changeStatus();
    },
    changeStatus() {
      if (this.gameStatus) {
        this.buttonString = "Start Game";
        // push score
        this.scores.unshift({
          id: this.scores.length,
          time: getNow(),
          level: this.level,
          score: this.score
        });
      } else {
        this.initGame();
        this.buttonString = "Stop Game";
      }
      this.gameStatus = !this.gameStatus;
    },
    changeSpeed() {
      this.currentSpeedIndex = (this.currentSpeedIndex + 1) % 3;
    },
    initGame() {
      this.level = 0;
      this.score = 0;
      this.Message = "";
      // this.currentClass = "show";
      this.buttonString = "Start Game";
      // this.setNewWord();
      this.error = "";
      this.setNextLevel();
    },
    setNextLevel() {
      this.level += 1;
      this.score += this.Message.length;
      this.currentClass = "show";
      const len = _.min([
        words_WordMaxLength,
        _.max([2, words_WordMinLength]) +
          _.floor((this.level - 1) / this.speeds[this.currentSpeedIndex].speed)
      ]);
      this.setNewWord(len);
    },
    setNewWord(len) {
      this.Message = _.sample(_.filter(words, (w) => w.length === len));
      this.Message = this.Message ? this.Message : ""; // if there is no word in the given length, the word will be empty and not undefined
    },
    timeoutEvent(e) {
      if (this.currentClass === "show") {
        this.currentClass = "ask";
      } else if (this.currentClass === "ask") {
        if (!e.Correct_Answer) {
          this.error = `<span>The word was <a href="https://www.dictionary.com/browse/${e.Origin}" target="_blank">'${e.Origin}'</a> and your answer was '${e.Answer}'</span>`;
          this.changeStatus();
        } else {
          this.setNextLevel();
        }
      }
    }
  }
};

function getNow() {
  const today = new Date();
  const month = today.getMonth() + 1;
  const date = `${today.getDate()} / ${month} / ${today.getFullYear()}`;
  const time = `${today.getHours()} : ${today.getMinutes()} : ${today.getSeconds()}`;
  const dateTime = `${time} , ${date}`;
  return dateTime;
}
</script>

<style>
#game {
  height: 100px;
  /* width: 15%; */
  padding: 10px 10px 10px 10px;
  /* background-color: aqua; */
}
</style>
