<template lang="pug">
  .container(@click="timoutEvent")
    transition
      div(ref="word" tabindex="0" :style='{animation: `fade_away ${Time}s linear forwards` }' @animationend="timoutEvent" @keyup.enter="timoutEvent")
        h2 Remember this: 
          span.word(style="color:red")
            | {{ Message }}
        //- slot
</template>

<script>
export default {
  props: {
    Message: String,
    Time: Number
  },
  data() {
    return {};
  },
  methods: {
    timoutEvent() {
      this.$emit("timeout");
    }
  },
  mounted() {
    this.$refs.word.focus();
  }
};
</script>

<style>
@keyframes fade_away {
  from {
    opacity: 1;
    pointer-events: none;
  }
  to {
    opacity: 0;
    pointer-events: none;
  }
}
div {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
</style>
