<template lang="pug">
  .container
    span.guess_phrase
    transition
      input(ref="guess" type='text' v-bind:value='guess' v-on:input="guess= $event.target.value" :style='{animation: `fade_away_after ${Time}s linear forwards` }' @animationend="timoutEvent" @keyup.enter="timoutEvent" autocorrect="off" autocapitalize="none")
</template>

<script>
export default {
  name: "TextGuess",
  props: {
    Message: String,
    Time: Number
  },
  data() {
    return {
      guess: ""
    };
  },
  methods: {
    timoutEvent() {
      this.$emit("timeout", {
        Correct_Answer: this.Message === this.guess,
        Answer: this.guess,
        Origin: this.Message
      });
    }
  },
  mounted() {
    // console.log(this.$refs);
    this.$refs.guess.focus();
    this.$refs.guess.select();
    this.$refs.guess.focus();
    this.$refs.guess.select();
  }
};
</script>

<style>
@keyframes fade_away_after {
  from {
    /* width: 100%; */
    background-color: crimson;
  }
  to {
    /* width: 0%; */
    background-color: rgb(204, 129, 144);
  }
}

.guess_phrase::before {
  content: "what was the word?";
  position: relative;
  color: red;
}
</style>
